.breadcrumbs {
	margin-bottom: rem(68);

	display: flex;
	align-items: center;

	@include min540 {
		margin-bottom: rem(40);
	}

	a, span {
		color: var(--c-text-gray);
		font-size: rem(15);

		@include min540 {
			font-size: rem(13);
		}
	}

	a {
		position: relative;
		margin-right: rem(37);

		&:hover {
			color: white;
		}

		&::before {
			content: "";
			position: absolute;
			top: rem(3);
			right: rem(-27);
			width: rem(18);
			height: rem(12);
			pointer-events: none;

			background: url(../img/breadcrumbs.svg) no-repeat;

			@include min540 {
				top: rem(2);
			}
		}
	}
}