.field_toggle {
	cursor: pointer;
	display: flex;
	align-items: center;

	flex-direction: row-reverse;

	input {
		display: none;

		&:checked {
			~ .check {
				background-color: var(--c-yellow);

				&::before {
					transform: translateX(85%);
				}
			}
		}
	}

	.check {
		position: relative;
		border-radius: rem(30);
		height: rem(30);
		width: rem(50);
		background-color: var(--c-black);

		transition: var(--transition);

		&::before {
			content: "";
			position: absolute;
			top: rem(1.8);
			left: rem(1.8);

			transform: translateX(0);

			width: rem(25);
			height: rem(25);
			border-radius: 50%;
			background-color: white;

			transition: var(--transition);

			box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
		}
	}

	.text {
		font-size: rem(15);
		color: var(--c-text-gray-light);
		margin-right: rem(20);
	}
}

.field_set {
	position: relative;

	&__visible {
		position: absolute;
		top: rem(19);
		right: rem(18);

		svg {
			
			path {
				fill: var(--c-text-gray);
				transition: var(--transition);
			}
		}

		&:hover {
			svg {
				path {
					fill: var(--c-text-gray-light);
				}
			}
		}
	}

	&__visible.active {
		svg {
			path {
				fill: var(--c-text-gray-light);
			}
		}
	}

	&__message {
		margin-top: rem(10);

		font-size: rem(13);
		color: rgba(178,177,190,.9);
	}
}

.field {
	width: 100%;
	height: rem(54);
	border-radius: var(--ui-border-4);
	padding: 0 rem(26);

	border: rem(1.5) solid var(--c-text-gray-light);
	background-color: transparent;
	color: var(--c-text-gray-light);
	font-size: rem(15);

	transition: var(--transition);

	&:focus {
		border-color: var(--c-yellow);
	}

	@include min768 {
		font-size: rem(13);
		padding: 0 rem(20);
	}

	@include placeholder {
		color: var(--c-text-gray-light);
	}
}

.field_checked {
	cursor: pointer;

	input {
		display: none;

		&:checked {
			~ .check {
			border-color: var(--c-yellow);
			background-color: var(--c-yellow);

			svg {
				opacity: 1;
			}
		}
		}
		
	}

	display: flex;
	
	.check {
		flex: 0 0 rem(25);
		width: rem(25);
		height: rem(25);
		border-radius: rem(2);
		background-color: transparent;
		border: rem(1) solid var(--c-text-gray-light);
		margin-right: rem(24);

		display: flex;
		align-items: center;
		justify-content: center;

		svg {
			opacity: 0;
			transition: var(--transition);
		}
	}

	.text {
		font-size: rem(13);
		line-height: 150%;
		color: var(--c-text-gray);

		span {
			color: var(--c-text-gray-light);
		}
	}
}