.custom_select {
	
	.ss-single-selected {
		height: rem(46) !important;
		background-color: #18172A !important;
		border-radius: rem(2) !important;
		border: rem(1) solid #8D8BA7 !important;
		padding: 0 rem(25) !important;

		transition: var(--transition) !important;

		.placeholder {
			color: var(--c-text-gray-light) !important;
			font-size: rem(15) !important;
		}

		.ss-arrow {
			margin: 0 0 0 rem(20) !important;

			@include min540 {
				margin: 0 0 0 rem(10) !important;
			}

			.arrow-down {
				border-color: var(--c-text-gray-light) !important;
			}
		}
	}

	.ss-open-below {
		border-bottom: 0 !important;

		~ .ss-content {
			border-top: 0 !important;

			.ss-list {
				border-top: 0 !important;

				.ss-option:first-child {
					padding: 16px 10px 7px 10px;
					border-top: rem(1) solid #8D8BA7;
				}
			}
		}
	}

	.ss-open-above {
		border-top: 0 !important;

		~ .ss-content {
			border-bottom: 0 !important;

			.ss-list {
				border-bottom: 0 !important;

				.ss-option:last-child {
					padding: 7px 10px 16px 10px;
					border-bottom: rem(1) solid #8D8BA7;
				}
			}
		}
	}

