.modal_overlay {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 50;
	
	width: 100%;
	height: 100%;
	background-color: var(--c-bg);
	overflow-y: auto;
	display: flex;
	visibility: hidden;
	pointer-events: none;

	padding: rem(42) rem(50);

	transform: translateX(-100%);

	transition: var(--transition);

	@include min991 {
		// display: flex;
		flex-direction: column;
	}

	@include min540 {
		padding: rem(28) rem(15);
	}

	&__close {
		position: absolute;
		top: rem(42);
		right: rem(50);

		@include min540 {
			top: rem(32);
			right: rem(15);

			svg {
				width: rem(28) !important;
				height: rem(28) !important;
			}
		}
	}

	&__header {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: rem(96);

		&_profie {
			width: rem(54);
			height: rem(54);
			border-radius: 50%;

			@include min540 {
				width: rem(40);
				height: rem(40);
			}
		}

		.button_yellow_border {
			position: absolute;
			top: rem(42);
			left: rem(50);
			width: rem(290);
			justify-content: center;

			@include min540 {
				// width: 100%;

				top: auto;
				bottom: rem(50);
				width: calc(100% - 84px);
			}

			@include min350 {
				width: calc(100% - 30px);
				left: rem(15);
			}
		}
	}

	&__nav {
		margin-left: auto;
		margin-right: auto;
		margin-top: rem(192);

		@include min540 {
			margin-top: 0;
		}

		@include min350 {
			margin-left: rem(50);
		}

		// @include min350 {
		// 	margin-left: 0;
		// }

		ul {
			li {
				text-align: center;
				margin-bottom: rem(40);

				@include min350 {
					margin-bottom: rem(25);
					text-align: left;
				}

				&:last-child {
					margin-bottom: 0;
				}

				a {
					color: var(--c-text-gray-light);
					font-size: rem(20);
					font-weight: 500;

					@include min350 {
						font-size: rem(15);
						font-weight: normal;
					}
				}
			}
		}
	}

}

.modal_overlay.active {
	@include min991 {
		visibility: visible;
		pointer-events: visible;

		transform: translateX(0%);
	}
}