.modal_profie {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 51;

	visibility: hidden;

	

	&__wrap {
		position: absolute;
		max-width: rem(600);
		width: 100%;
		top: 0;
		transform: translateY(-100%);
		left: 50%;
		background-color: #211F3D;
		border-radius: rem(5);
		margin-left: rem(-300);

		padding-bottom: rem(60);

		transition: var(--transition);

		overflow-y: auto;

		@include min768 {
			left: 0;
			height: 100%;
			max-width: 100%;
			margin-left: 0;

			padding-top: rem(80);
		}

		.js--modal-close {
			position: absolute;
			top: rem(15);
			right: rem(15);
		}
	}

	&__tab {
		position: relative;
		margin-left: rem(85);
		margin-right: rem(85);

		margin-bottom: rem(45);

		@include min768 {
			margin-left: rem(15);
			margin-right: rem(15);
			margin-bottom: rem(54);
		}

		
		display: flex;

		&::before {
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: rem(5);
			border-radius: rem(5);
			background-color: rgba(178, 177, 190, .2);

			@include min768 {
				height: rem(4);
			}
		}

		button {
			position: relative;
			flex: 0 0 50%;
			max-width: 50%;
			padding: rem(44) 0;

			display: flex;
			justify-content: center;

			font-size: rem(25);
			font-weight: 500;
			color: var(--c-text-gray-light);

			@include min768 {
				font-size: rem(20);
				padding: rem(22) 0;
			}

			&::before {
				content: "";
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;
				height: rem(5);
				border-radius: rem(5);
				background-color: var(--c-yellow);
				opacity: 0;
				z-index: 2;
			}
		}

		button.active {
			color: var(--c-yellow);
		
			&::before {
				opacity: 1;
			}
		}

	}

	&__wrap_tb {
		// display: flex;
		display: none;
		flex-direction: column;

		padding: 0 rem(85);

		@include min768 {
			padding: 0 rem(15);
		}

		.field_set {
			margin-top: rem(20);

			@include min768 {
				margin-bottom: rem(14);
			}

			&:first-child {
				margin-top: 0;
			}
		}

		.forgot {
			margin-top: rem(15);
			color: var(--c-yellow);
			font-size: rem(15);
		}

		.button_yellow {
			margin-top: rem(40);
		}

		.field_checked {
			margin-top: rem(50);
		}
	}

	&__wrap_tb.active {
		display: flex;
	}
}

.modal_profie.active {
	animation: modal_profile_active 500ms forwards;

	.modal_profie__wrap {
		transition: var(--transition);
		top: 50%;
		transform: translateY(-50%);

		@include min768 {
			top: 0;
			transform: translateY(0%);
		}
	}
}

@keyframes modal_profile_active {
	0% {
		visibility: hidden;
		opacity: 0;
	}

	100% {
		opacity: 1;
		visibility: visible;
	}
}