.tb_item {
	background-color: rgba(141, 139, 167, .1);
	height: rem(75);

	display: flex;
	align-items: center;

	.title {
		color: white;
	}
}



.table_order {
	display: flex;
	flex-direction: column;
	
	.w-10 {
		flex: 0 0 7.66%;
		

		@include max991 {
			max-width: 7.66%;
		}

		@include min1200 {
			flex: 1;
		}

		
	}
	.w-20 {
		flex: 0 0 15.39%;

		@include max991 {
			max-width: 15.39%;
		}

		@include min1200 {
			flex: 1;
		}
		
	}
	.w-30 {
		flex: 0 0 24.38%;

		@include max991 {
			max-width: 24.38%;
		}

		@include min1200 {
			flex: 1;
		}
	}
	.w-50 {
		flex: 0 0 41.64%;

		@include max991 {
			max-width: 41.64%;
		}

		@include min1200 {
			flex: 1;
		}
	}

	&__header {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		margin-bottom: rem(20);

		@include min540 {
			margin-right: rem(-7);
		}

		> span {
			display: flex;
			align-items: center;

			color: var(--c-text-gray-light);

			@include min991 {
				white-space: nowrap;
			}

			&:nth-child(2) {
				@include min540 {
					color: var(--c-text-gray);
				}
			}

			&:nth-child(3) {

				@include min991 {
					margin-left: auto;
					// margin-right: rem(15);
					justify-content: flex-end;

					margin-right: rem(14);
				}

				@include min540 {
					flex: 0 0 19%;
					max-width: 19%;
					justify-content: flex-end;

					color: var(--c-text-gray);
				}

				span {

					&:first-child {
						@include min991 {
							display: none;
						}
					}

					&:last-child {
						display: none;

						@include min991 {
							display: block;
						}
					}
				}
			}

			&:nth-child(4) {
				@include min991 {
					display: none;
				}
			}

			&:nth-child(5) {
				@include min991 {
					justify-content: flex-end;
					flex: 0;
				}

				@include min540 {
					flex: 0 0 rem(54);
					max-width: rem(54);
					justify-content: flex-end;

					color: var(--c-text-gray);
				}
			}

			@include min1280 {
				font-size: rem(15);

				&:first-child {
					display: none;
				}
			}

			@include min540 {
				font-size: rem(13);
			}

			svg {
				margin-left: rem(6);
			}
		}
	}

	&__rows {
		display: flex;
		flex-direction: column;

		@include min540 {
			margin: 0 -15px;
		}
	}

	&__row {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		@include min991 {
			background-color: rgba(141, 139, 167, .1);
			padding: rem(10) 0;

			&:nth-child(2n) {
				background-color: transparent;
			}
		}

		@include min540 {
			padding: rem(10) rem(15);
		}

		&:nth-child(2n) {
			.tb_item {
				background-color: transparent;
			}
		}

		.tb_item {

			@include min991 {
				background-color: transparent;
				height: auto;

				// white-space: nowrap;

				&:first-child {
					flex: 0 0 100%;
					max-width: 100%;
					
					padding: 0 rem(14);

					@include min540 {
						padding: 0;
					}

					.title {
						font-size: rem(13);
						font-weight: 500;
						color: var(--c-text-gray);
					}
				}

				&:nth-child(2) {
					flex: 0 0 60%;
					max-width: 60%;
					height: auto;

					padding: rem(12) rem(14);

					@include min540 {

						padding: rem(10) 0;

						flex: 0 0 rem(175);
						max-width: rem(175);
						.text-overflow {
							// white-space: normal;
							// max-height: rem(38);
							// max-width: rem(182);
						}
					}
					
				}

				&:nth-child(3) {
					margin-left: auto;

					@include min991 {
						justify-content: flex-end;
						margin-right: rem(22);
					}

					// @include min540 {
					// 	align-items: flex-start;
					// 	padding-top: rem(14);
					// }

					@include min540 {
						flex: 0 0 rem(47);
						max-width: rem(47);
						align-items: flex-start;
						justify-content: center;

						margin-right: rem(14);

						.title {
							color: var(--c-text-gray);
							font-weight: 500;
						}
					}
				}

				&:nth-child(4) {
					flex: 0 0 100%;
					max-width: 100%;
					height: auto;

					order: 9999;

					padding: 0;
					padding: rem(14) 0 0 rem(14);

					border-top: rem(1.5) solid var(--c-text-dark);

					@include min540 {
						padding: rem(14) 0 0 0;
					}
				}

				&:nth-child(5) {
					@include min991 {
						justify-content: flex-end;
						flex: 0;
						padding-right: rem(14);
					}

					@include min540 {
						padding-right: 0;
						flex: 0 0 rem(48);
						max-width: rem(48);
						align-items: flex-start;
						justify-content: flex-start;

						.title {
							color: var(--c-text-gray);
							font-weight: 500;
						}
					}

					.title {
						white-space: nowrap;
					}
				}
			}
			

			&:first-child {
				border-top-left-radius: rem(2);
			}

			&:last-child {
				border-top-right-radius: rem(2);
			}
		}
	}
}