.page_game_filter {
	margin-bottom: rem(200);

	.page_nav {
		margin-top: rem(50);

		justify-content: center;

	}

	.sect_header {
		position: relative;
		display: flex;
		flex-direction: column;

		margin-bottom: rem(112);

		@include min540 {
			margin-bottom: rem(50);
		}

		.filter_toggle_block {
			display: flex;
			flex-direction: column;
		}

		> .button_yellow_border {
			position: absolute;
			bottom: 0;
			right: 0;

			@include min1050 {
				bottom: auto;
				top: 0;
			}

			@include min540 {
				position: relative;
				top: auto;
				margin-top: rem(30);
				justify-content: center;

				span {
					font-size: rem(15);
					font-weight: normal;
				}
			}
		}

		&__title {
			display: flex;
			align-items: center;

			margin-bottom: rem(78);

			@include min540 {
				margin-bottom: rem(30);
			}

			.title_h2 {
				color: var(--c-text-gray-light);
			}
			.icon-search {
				margin-left: rem(24);

				@include min540 {
					width: rem(40);
					height: rem(40);
					border-radius: 50%;

					display: flex;
					align-items: center;
					justify-content: center;

					background-color: var(--c-yellow);

					svg {
						width: rem(21);
						height: rem(21);
					}
				}

				svg {
					path {
						fill: var(--c-yellow);

						@include min540 {
							fill: var(--c-text-dark);
						}
					}
				}
			}

			.icon-filter {
				display: none;

				@include min540 {

					width: rem(40);
					height: rem(40);
					border-radius: 50%;

					margin-left: rem(20);

					filter: drop-shadow(0px 0px 7px rgba(243, 207, 137, 0.8));

					display: flex;
					align-items: center;
					justify-content: center;

					background-color: var(--c-yellow);
				}
			}
		}

		&__tab {
			margin-bottom: rem(30);

			display: flex;
			flex-wrap: wrap;

			@include min540 {
				margin-bottom: rem(15);
			}

			.button_gray_border {
				margin-right: rem(10);

				@include min540 {
					flex: 1;
					padding: 0 rem(15);

					span {
						font-size: rem(13);
					}
				}

				&:last-child {
					margin-right: 0;
				}
			}
		}

		&__list {
			margin-bottom: rem(64);

			@include min991 {
				display: none;
			}

			ul {
				display: flex;
				flex-wrap: wrap;

				li {
					margin-right: rem(40);

					&:last-child {
						margin-right: 0;
					}

					a {
						font-size: rem(15);
						color: white;
						padding-bottom: rem(2);

						&:hover {
							border-bottom: rem(1.5) solid var(--c-yellow);
						}
					}

					a.active {
						border-bottom: rem(1.5) solid var(--c-yellow);
					}
				}
			}
		}

		&__selected {
			display: flex;
			align-items: center;

			@include min768 {
				flex-wrap: wrap;
			}

			.custom_select {
				align-self: flex-start;
				width: auto;
				margin-right: rem(10);

				@include min540 {
					flex: 1;
					
					
				}

				&:last-child {
					margin-right: 0;
				}
			}

			.md-mr-0 {
				@include min540 {
					margin-right: 0;
				}
			}

			.field_toggle {
				margin-left: rem(28);

				@include min768 {
					margin-left: 0;
					margin-top: rem(15);
					justify-content: flex-end;
					flex: 0 0 100%;
					max-width: 100%;
				}
			}
		}
	}

}

.user_card {
	display: flex;
	align-items: center;

	&__image {
		flex: 0 0 rem(54);
		width: rem(54);
		height: rem(54);
		border-radius: 50%;

		margin-right: rem(13);

		border: rem(1) solid var(--c-yellow);

		display: flex;
		align-items: center;
		justify-content: center;

		@include min1280 {
			flex: 0 0 rem(45);
			width: rem(45);
			height: rem(45);
		}

		@include min991 {
			flex: 0 0 rem(40);
			width: rem(40);
			height: rem(40);
		}

		img {
			max-width: rem(34);

			@include min991 {
				max-width: rem(25);
			}
		}
	}

	&__right {
		display: flex;
		flex-direction: column;

		.title {
			position: relative;
			font-size: rem(15);
			color: var(--c-text-gray-light);
			word-break: break-all;
			max-width: rem(200);

			align-self: flex-start;

			@include min1280 {
				font-size: rem(13);
			}

			@include min991 {
				max-width: 100%;
			}

			@include min540 {
				font-size: rem(13);
				
			}

			.online {
				position: absolute;
				top: rem(-2);
				right: rem(-12);
				flex: 0 0 rem(8);
				width: rem(8);
				height: rem(8);
				border-radius: 50%;
				background-color: #27AE60;

				@include min1280 {
					flex: 0 0 rem(5);
					width: rem(5);
					height: rem(5);
					right: rem(-7);
				}
			}
		}

		.rating {
			margin-right: rem(5);
		}

		.date {
			color: var(--c-text-gray);
			font-size: rem(13);
			white-space: nowrap;

			@include min1280 {
				display: none;
			}
		}
	}
}