*,
*::after,
*::before {
  box-sizing: border-box;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

body {
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
}

ol,
ul {
  padding: 0;
  list-style: none;
}

a {
  font-family: inherit;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

@font-face {
  font-family: 'Gotham';
  src: local("Gotham Light"), local("Gotham-Light"), url("../fonts/Gotham/Gotham-Light.woff2") format("woff2");
  font-style: normal;
  font-weight: 300;
  font-display: swap;
}

@font-face {
  font-family: 'Gotham';
  src: local("Gotham Book"), local("Gotham-Book"), url("../fonts/Gotham/Gotham-Book.woff2") format("woff2");
  font-style: normal;
  font-weight: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Gotham';
  src: local("Gotham Medium"), local("Gotham-Medium"), url("../fonts/Gotham/Gotham-Medium.woff2") format("woff2");
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}

@font-face {
  font-family: 'Gotham';
  src: local("Gotham Bold"), local("Gotham-Bold"), url("../fonts/Gotham/Gotham-Bold.woff2") format("woff2");
  font-style: normal;
  font-weight: bold;
  font-display: swap;
}

@font-face {
  font-family: 'Gotham';
  src: local("Gotham Black"), local("Gotham-Black"), url("../fonts/Gotham/Gotham-Black.woff2") format("woff2");
  font-style: normal;
  font-weight: 900;
  font-display: swap;
}

:root {
  --f-default: 'Gotham', sans-serif;
  --c-bg: #18172B;
  --c-black: #2D2B49;
  --c-text-gray: #B2B1BE;
  --c-blue-accent: #2F23F4;
  --c-text-dark: #18172A;
  --c-text-gray-light: #E0DEF4;
  --c-yellow: #F3CF89;
  --c-pink: #EB00FF;
  --transition: 250ms ease;
  --ui-border-4: 4px;
}

* {
  outline: none;
  border: 0;
  margin: 0;
  padding: 0;
}

html, body {
  position: relative;
  overflow-x: hidden;
}

html {
  font-size: 100%;
}

button {
  background-color: transparent;
  font-size: inherit;
}

a {
  text-decoration: none;
}

a, button {
  transition: var(--transition);
}

a *, button * {
  transition: var(--transition);
}

li {
  list-style: none;
}

body {
  position: relative;
  font-family: var(--f-default);
  font-size: 1.0625rem;
  font-weight: normal;
  background-color: var(--c-bg);
  min-width: 320px;
  overflow-x: hidden;
}

.relative {
  position: relative;
}

