.modal_profie {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 51;
  visibility: hidden;
}

.modal_profie__wrap {
  position: absolute;
  max-width: 37.5rem;
  width: 100%;
  top: 0;
  transform: translateY(-100%);
  left: 50%;
  background-color: #211F3D;
  border-radius: 0.3125rem;
  margin-left: -18.75rem;
  padding-bottom: 3.75rem;
  transition: var(--transition);
  overflow-y: auto;
}

@media (max-width: 767px) {
  .modal_profie__wrap {
    left: 0;
    height: 100%;
    max-width: 100%;
    margin-left: 0;
    padding-top: 5rem;
  }
}

.modal_profie__wrap .js--modal-close {
  position: absolute;
  top: 0.9375rem;
  right: 0.9375rem;
}

.modal_profie__tab {
  position: relative;
  margin-left: 5.3125rem;
  margin-right: 5.3125rem;
  margin-bottom: 2.8125rem;
  display: flex;
}

@media (max-width: 767px) {
  .modal_profie__tab {
    margin-left: 0.9375rem;
    margin-right: 0.9375rem;
    margin-bottom: 3.375rem;
  }
}

.modal_profie__tab::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0.3125rem;
  border-radius: 0.3125rem;
  background-color: rgba(178, 177, 190, 0.2);
}

@media (max-width: 767px) {
  .modal_profie__tab::before {
    height: 0.25rem;
  }
}

.modal_profie__tab button {
  position: relative;
  flex: 0 0 50%;
  max-width: 50%;
  padding: 2.75rem 0;
  display: flex;
  justify-content: center;
  font-size: 1.5625rem;
  font-weight: 500;
  color: var(--c-text-gray-light);
}

@media (max-width: 767px) {
  .modal_profie__tab button {
    font-size: 1.25rem;
    padding: 1.375rem 0;
  }
}

.modal_profie__tab button::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0.3125rem;
  border-radius: 0.3125rem;
  background-color: var(--c-yellow);
  opacity: 0;
  z-index: 2;
}

.modal_profie__tab button.active {
  color: var(--c-yellow);
}

.modal_profie__tab button.active::before {
  opacity: 1;
}

.modal_profie__wrap_tb {
  display: none;
  flex-direction: column;
  padding: 0 5.3125rem;
}

@media (max-width: 767px) {
  .modal_profie__wrap_tb {
    padding: 0 0.9375rem;
  }
}

.modal_profie__wrap_tb .field_set {
  margin-top: 1.25rem;
}

@media (max-width: 767px) {
  .modal_profie__wrap_tb .field_set {
    margin-bottom: 0.875rem;
  }
}

.modal_profie__wrap_tb .field_set:first-child {
  margin-top: 0;
}

.modal_profie__wrap_tb .forgot {
  margin-top: 0.9375rem;
  color: var(--c-yellow);
  font-size: 0.9375rem;
}

.modal_profie__wrap_tb .button_yellow {
  margin-top: 2.5rem;
}

.modal_profie__wrap_tb .field_checked {
  margin-top: 3.125rem;
}

.modal_profie__wrap_tb.active {
