}

.modal_profie.active {
  animation: modal_profile_active 500ms forwards;
}

.modal_profie.active .modal_profie__wrap {
  transition: var(--transition);
  top: 50%;
  transform: translateY(-50%);
}

@media (max-width: 767px) {
  .modal_profie.active .modal_profie__wrap {
    top: 0;
    transform: translateY(0%);
  }
}

@keyframes modal_profile_active {
  0% {
    visibility: hidden;
    opacity: 0;
  }
  100% {
    opacity: 1;
    visibility: visible;
  }
}

.modal_overlay {
