.modal_new {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;

	z-index: 49;

	background-color: var(--c-bg);
	// display: none;
	opacity: 0;
	overflow-y: auto;


	// display: flex;
	// align-items: center;
	// justify-content: center;

	transition: var(--transition);
	
	.container {
		// position: relative;

		@include min1200 {
			margin: 0;
		}
	}

	&__close {
		position: absolute;
		top: rem(40);
		right: rem(40);
		z-index: 2;

		@include min540 {
			top: rem(24);
			right: rem(15);
		}
	}

	&__wrap {
		box-shadow: 0px 0px 50px #8D8BA7;
		border-radius: rem(20);
		overflow: hidden;
		width: 100%;
		max-height: rem(700);
		transition: var(--transition);

		// transform: translateY(-50%);

		max-width: 1280px;

		@include min1200 {
			box-shadow: none;
			border-radius: 0;
			max-height: 100%;

			overflow-y: auto;
		}

		@include min540 {
			border-radius: 0;
		}
		

		&_header {
			position: relative;
			height: rem(272);
			width: 100%;
			background-size: cover;

			padding: rem(80) rem(88) rem(50) rem(80);

			@include min540 {
				padding: rem(224) rem(15) 0 rem(15);
				background-position: top center;
			}

			&::before {
				content: "";
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;
				height: rem(100);
				background: linear-gradient(0deg, #18172A 0%, rgba(24, 23, 42, 0) 100%);
			}

			.title {
				position: relative;
				z-index: 2;
				font-size: rem(40);
				color: white;
				font-weight: 500;
				line-height: 150%;
				max-width: rem(634);

				margin-bottom: rem(12);

				@include min540 {
					font-size: rem(30);
					max-width: 100%;
				}
			}

			.date {
				position: relative;
				z-index: 2;
				display: flex;
				align-items: center;

				.icon {
					margin-right: rem(10);
				}

				span {
					color: var(--c-text-gray);
					line-height: 150%;
				}
			}
		}

		&_content {
			padding: rem(70) rem(88) rem(100) rem(88);

			overflow-y: auto;
			max-height: rem(375);

			@include min1200 {
				max-height: 100%;
			}

			@include min540 {
				padding: 0 rem(15) rem(70) rem(15);
				margin-top: rem(170);
			}

			p {
				color: var(--c-text-gray-light);
				line-height: 150%;
			}
		}

		&_content.suffle {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			p {
				max-width: rem(349);
				margin-bottom: rem(38);
			}
		}
	}
}

.modal_new.active {
	// display: flex;
	animation: opacityTrans 500ms forwards;
	transition: var(--transition);

	.modal_new__wrap {
		position: absolute;
		top: 50%;
		animation: translateYmodal 300ms forwards;
		// transform: translateY(50%);
		// transition: all 1s ease;

		@include min1200 {
			top: 0;

			animation: translateYmodal_1050 300ms forwards;
		}
	}
}

.modal_new.modal_new_remove {
	animation: opacityTransOut 1s forwards;
	transition: var(--transition);

	.modal_new__wrap {
		animation: translateYmodalOut 250ms forwards;
	}
}

@keyframes opacityTrans {
	0% {
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}

@keyframes opacityTransOut {
	0% {
		opacity: 1;
	}

	100% {
		opacity: 0;
	}
}


@keyframes translateYmodal {
	0% {
		transform: translateY(-100%);
	}

	100% {
		transform: translateY(-50%);
	}
}

@keyframes translateYmodal_1050 {
	0% {
		transform: translateY(-100%);
	}

	100% {
		transform: translateY(0%);
	}
}

@keyframes translateYmodalOut {
	0% {
		transform: translateY(-50%);
	}

	100% {
		transform: translateY(-100%);
	}
}