.button_yellow_border {
	padding: 0 rem(35);
	height: rem(54);
	border-radius: rem(4);
	display: flex;
	align-items: center;
	border: rem(1.5) solid var(--c-yellow);

	&:hover {
		background-color: var(--c-yellow);

		span {
			color: var(--c-black);
		}

		.arrow {
			svg {
				path {
					fill: var(--c-black);
				}
			}
		}
	}

	span {
		font-weight: bold;
		color: var(--c-text-gray-light);
	}

	.arrow {
		margin-left: rem(7.5);

		svg {
			path {
				fill: var(--c-text-gray-light);
			}
		}
	}
}

.button_gray_border {
	padding: 0 rem(35);
	height: rem(56);

	border-radius: rem(2);

	display: flex;
	align-items: center;
	justify-content: center;

	border: rem(1.5) solid var(--c-black);

	&:hover {
		background-color: var(--c-black);

		span {
			color: white;
		}
	}

	span {
		font-weight: 500;
		color: var(--c-text-gray);
	}
}

.button_gray_border.active {
	background-color: var(--c-black);

	span {
		color: white;
	}
}

.button_yellow {
	padding: 0 rem(35);
	height: rem(54);

	border-radius: rem(4);

	display: flex;
	align-items: center;
	justify-content: center;
	background-color: var(--c-yellow);

	&:hover {
		background-color: #FFDD9A;
	}

	span {
		color: var(--c-text-dark);
		font-weight: 500;
	}
}